@font-face {
  font-family: "Houschka";
  src: url("../fonts/HouschkaPro-Light.woff2"), url("../fonts/HouschkaPro-Light.woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Houschka";
  src: url("../fonts/HouschkaPro-Medium.woff2"), url("../fonts/HouschkaPro-Medium.woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Houschka";
  src: url("../fonts/HouschkaPro-Bold.woff2"), url("../fonts/HouschkaPro-Bold.woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Houschka";
  src: url("../fonts/HouschkaPro-ExtraBold.woff2"), url("../fonts/HouschkaPro-ExtraBold.woff");
  font-weight: 800;
  font-style: normal;
}
* {
  box-sizing: border-box;
  outline: none;
}
body {
  font-family: 'Montserrat', Arial, sans-serif;
  color: #1E1E1E;
}
a {
  text-decoration: none;
}
h2 {
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 36px;
  line-height: 125%;
  letter-spacing: 0.03em;
  color: #202020;
  margin: 0;
}
input[type="checkbox"],
input[type="file"] {
  display: none;
}
input[type="checkbox"] + label {
  position: relative;
}
input[type="checkbox"] + label::before {
  content: '';
  display: block;
  width: 35px;
  height: 36px;
  background: url('../img/svg/check-off.svg') no-repeat;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
input[type="checkbox"]:checked + label::before {
  background: url('../img/svg/check-on.svg') no-repeat;
  position: absolute;
  left: 0;
}
::-webkit-scrollbar {
  display: none;
}
.start-btn {
  width: 307px;
  height: 70px;
  position: relative;
  text-align: center;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  letter-spacing: 0.15em;
  color: #FFFFFF;
  background: linear-gradient(180deg, #FFCA46 0%, #FF9200 100%);
  border-radius: 0px 50px 50px 50px;
  border: none;
  transition: all 0.3s ease;
  cursor: pointer;
}
.start-btn-arrow {
  display: block;
  width: 48px;
  height: 8px;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}
.start-btn:hover,
.start-btn:focus {
  box-shadow: 0px 0px 15px #FF8A00;
  transition: all 0.3s ease;
}
.start-btn:active {
  border: 3px solid #049DD9;
  color: #474747;
  background: #FFFFFF;
  transition: all 0.3s ease;
}
.start-btn-arrow {
  fill: #FAFAFA;
  transition: all 0.3s ease;
}
.start-btn:active .start-btn-arrow {
  fill: #474747;
  transition: all 0.3s ease;
}
.phone-block {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.phone-btn-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 225px;
  margin-bottom: 8px;
}
.phone-btn-wrap button {
  margin-right: 10px;
  border: none;
  background: transparent;
  padding: 0;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 140%;
  letter-spacing: 0.05em;
  color: #BDBDBD;
  cursor: pointer;
}
.phone-region-btn.active {
  text-decoration-line: underline;
  color: #202020;
}
.phone {
  display: none;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  color: #202020;
  transition: all 0.3s ease;
  cursor: pointer;
}
.phone:hover,
.phone:focus {
  color: #FF7A00;
  transition: all 0.3s ease;
}
.phone.active {
  display: block;
}
.lang-btn {
  display: flex;
  padding-right: 0;
  position: relative;
  border: none;
  background: #FFFFFF;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  text-transform: capitalize;
  color: #202020;
  transition: all 0.3s ease;
  cursor: pointer;
}
.lang-btn:hover,
.lang-btn:focus {
  opacity: 0.6;
  transition: all 0.3s ease;
}
.polish-lang {
  padding-left: 10px;
  position: relative;
}
.polish-lang::before {
  content: '|';
  font-family: 'Houschka', Arial, sans-serif;
  font-weight: 700;
  position: absolute;
  left: 0;
  top: -2px;
}
.language-name {
  font-weight: 700;
  margin-right: 5px;
}
.container {
  width: 95%;
  padding: 0 50px;
  margin: 0 auto;
  max-width: 1850px;
}
.header {
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 110px;
  background-color: #FFFFFF;
  opacity: 0;
  transition: all 1.5s ease;
}
.header.onload {
  opacity: 1;
  box-shadow: 0 0 10px #cacaca;
  transition: all 1.5s ease;
}
.header__main-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
.header__start-btn {
  position: relative;
  min-width: 212px;
  height: 40px;
  padding: 5px 20px 5px 35px;
  border-top: 0 solid #FF8A00;
  border-bottom: 0 solid #FF8A00;
  border-left: 4px solid #FF8A00;
  border-right: 4px solid #FF8A00;
  background: #FFFFFF;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: 0.05em;
  color: #FF8A00;
  transition: all 0.1s ease;
  cursor: pointer;
}
.header__logo {
  display: flex;
  align-items: center;
  height: 100%;
}
.header__logo svg {
  height: 85px;
}
.header__logo-stripes {
  fill: #505050;
  transition: all 0.3s ease;
}
.header__logo-shining {
  opacity: 0;
  transition: all 0.3s ease;
}
.header__logo:hover .header__logo-shining,
.header__logo:focus .header__logo-shining {
  opacity: 1;
  transition: all 0.3s ease;
}
.header__logo:active .header__logo-shining {
  opacity: 0;
  transition: all 0.3s ease;
}
.header__logo:hover .header__logo-stripes,
.header__logo:focus .header__logo-stripes {
  fill: #FFD335;
  transition: all 0.3s ease;
}
.header__start-btn-plus {
  width: 9px;
  height: 11px;
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-45%);
  fill: #FF8A00;
  transition: all 0.3s ease;
}
.header__start-btn:hover,
.header__start-btn:focus {
  border-top: 4px solid #FF8A00;
  border-bottom: 4px solid #FF8A00;
  transition: all 0.3s ease;
}
.header__lang-wrap {
  display: flex;
  justify-content: space-between;
  width: 65px;
}
.active-lang {
  font-weight: 700;
}
.contact-form__wrap.opened-form {
  transform: translateX(0);
}
.contact-form__wrap {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  height: 100vh;
  overflow-y: scroll;
  width: 100%;
  background: rgba(0, 0, 0, 0.31);
  z-index: 15;
  transform: translateX(-120%);
  transition: transform 0.5s ease;
}
.contact-form__wrap .container {
  height: calc(100vh - 110px);
  padding: 50px 60px;
  position: relative;
  background: #FFFFFF;
  overflow-y: scroll;
}
.contact-form__wrap::before {
  content: '';
  display: block;
  position: absolute;
  width: 50%;
  height: calc(100vh - 110px);
  background: #FFFFFF;
}
.contact-form {
  width: 100%;
}
.contact-form__desc {
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 180%;
  letter-spacing: 0.03em;
  color: #474747;
}
.contact-form__input-wrap {
  display: grid;
  grid-template-columns: 35% 65%;
  grid-template-rows: 33% 33% 33%;
  width: 95%;
  height: auto;
  color: #1E1E1E;
  font-size: 20px;
}
.contact-form__input-wrap input {
  height: 45px;
}
.contact-form__input-wrap input,
.contact-form__input-wrap textarea {
  margin-top: 2px;
}
.contact-form__text-wrap,
.contact-form__textarea-wrap {
  display: flex;
  flex-direction: column;
}
.contact-form__textarea-wrap textarea {
  height: 83.5%;
  resize: none;
  border: 1px solid #B6B6B6;
  padding: 10px;
}
.contact-form__textarea-wrap {
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: -1;
}
.contact-form__input-wrap > div:not(:last-child) {
  grid-column-start: 1;
}
.contact-form__text-input {
  width: 90%;
  background: #ffffff;
  border: 1px solid #B6B6B6;
  padding: 10px 35px 10px 10px;
  font-family: 'Houschka', Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  letter-spacing: 0.03em;
}
.contact-form__text-input:valid {
  background: url("../img/svg/valid.svg") no-repeat 98%;
}
.contact-form__text-input:invalid {
  background: url("../img/svg/invalid.svg") no-repeat 98%;
}
.contact-form__text-input::placeholder {
  font-family: 'Houschka', Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  letter-spacing: 0.03em;
  color: #707070;
  opacity: 0.7;
}
.contact-form__label {
  display: flex;
  width: 61%;
  padding-left: 60px;
  margin: 1% 0 1% 0;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 180%;
  letter-spacing: 0.03em;
  color: #474747;
  cursor: pointer;
}
.close-btn {
  width: 25px;
  height: 25px;
  padding: 0;
  border: 0;
  background: none;
  position: absolute;
  right: 60px;
  top: 50px;
  fill: #474747;
  cursor: pointer;
  transition: all 0.3s ease;
}
.close-btn:hover svg,
.close-btn:focus svg {
  opacity: 0.6;
  transform: rotate(90deg);
  transition: all 0.3s ease;
}
.close-btn svg {
  width: 25px;
  height: 25px;
}
.contact-form__interactive-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
}
.contact-form__file-wrap {
  width: auto;
  max-width: 30%;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
}
.contact-form__file-wrap:hover,
.contact-form__file-wrap:focus {
  opacity: 0.6;
  transition: all 0.3s ease;
}
.contact-form__file-label {
  display: block;
  max-width: 100%;
  height: 25px;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  letter-spacing: 0.025em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
  color: #9A9A9A;
  font-weight: 600;
  font-size: 20px;
  line-height: 25px;
}
.contact-form__paperclip {
  width: 25px;
  height: 25px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.contact-form__submit {
  color: #FF8A00;
  background: #FFFFFF;
  border: 4px solid #FF8A00;
  transition: all 0.3s ease;
}
.contact-form__submit:hover,
.contact-form__submit:focus {
  background: linear-gradient(180deg, #FFA829 0%, #FF6D01 100%);
  color: #FFFFFF;
  transition: all 0.3s ease;
}
.greetings {
  min-height: 540px;
  height: 100vh;
  padding-top: 110px;
  background: #fafafa;
}
.greetings__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  position: relative;
  transition: all 1.5s ease;
}
.greetings__background-wrap {
  height: 100%;
}
.greetings__desc-block {
  padding: 35px 0 0;
  max-width: 510px;
  position: relative;
}
.greetings__desc-block::before {
  content: '';
  display: block;
  width: 67px;
  height: 5px;
  position: absolute;
  left: 0;
  top: 100px;
  background: #FF961B;
  opacity: 0;
  transition: all 0.3s ease;
}
.greetings__desc-block.onload::before {
  top: 0;
  opacity: 1;
  transition: all 0.3s ease;
}
.greetings__text {
  margin: 0;
  font-family: 'Houschka Pro', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 125.9%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #1E1E1E;
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.5s ease;
}
.greetings__text.onload {
  opacity: 1;
  transform: translateY(0);
  transition: all 0.5s ease;
}
.greetings__title {
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 48px;
  line-height: 135%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.5s ease;
}
.greetings__title.onload {
  transform: translateY(0px);
  opacity: 1;
  transition: all 0.5s ease;
}
.greetings__start-btn {
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.5s ease;
}
.greetings__start-btn.onload {
  transform: translateY(0px);
  opacity: 1;
  transition: all 0.5s ease;
}
.greetings__title-accent {
  color: #FF961B;
}
.greetings__pic-wrap {
  height: 90%;
  max-height: 550px;
  width: 50%;
}
.greetings__mouse-icon-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  padding: 0;
  position: absolute;
  left: 50%;
  bottom: 15px;
  transform: translateX(-50%);
  list-style: none;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.greetings__mouse-icon {
  fill: #000;
}
.greetings__mouse-icon circle {
  animation: scroll 1.5s ease infinite;
}
.greetings__pic-wrap {
  position: relative;
}
@keyframes scroll {
  0% {
    opacity: 0;
    cy: 231;
  }
  33% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    cy: 431;
  }
}
.footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 370px;
  padding-bottom: 30px;
  background-color: #202020;
}
.footer .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.footer__title-block {
  display: flex;
  align-items: center;
  height: 30%;
  background-color: #FFFFFF;
}
.footer__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.footer__copyright {
  width: auto;
  font-family: 'Houschka', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.7);
}
.footer .phone-block {
  width: auto;
}
.footer__wrap .phone {
  font-weight: 600;
  font-size: 25px;
  line-height: 40px;
  color: #FFFFFF;
}
.footer .contact-form__label::before {
  background: url('../img/svg/check-off-footer.svg') no-repeat;
}
#footer-policy:checked + .contact-form__label::before {
  background: url('../img/svg/check-on-footer.svg') no-repeat;
}
.footer__logo-circle {
  opacity: 0;
  transition: all 0.3s ease;
}
.footer__logo:hover .footer__logo-circle,
.footer__logo:focus .footer__logo-circle {
  opacity: 1;
  transition: all 0.3s ease;
}
.footer__logo-stripes {
  fill: #505050;
  transition: all 0.3s ease;
}
.footer__logo:hover .footer__logo-stripes,
.footer__logo:focus .footer__logo-stripes {
  fill: #FFD335;
  transition: all 0.3s ease;
}
.footer__mail {
  display: block;
  margin: 20px 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 80px;
  color: #FF7A00;
}
.footer__mail:hover,
.footer__mail:focus {
  text-decoration: underline;
}
.social {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30px;
  margin: 0;
  padding: 0;
}
.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 0;
  transition: all 0.3s ease;
}
.social-link svg {
  width: 30px;
  height: 30px;
  fill: #FAFAFA;
  transition: all 0.3s ease;
}
.social-link:hover svg,
.social-link:focus svg {
  fill: #FF8A00;
  filter: drop-shadow(0 0 5px #FF8A00);
  transition: all 0.3s ease;
}
.social--vk,
.social--vk svg {
  height: 20px;
}
.social__item {
  display: flex;
  height: 100%;
  width: 25%;
}
.contact {
  display: flex;
  align-items: center;
  height: 100vh;
  min-height: 670px;
}
